import React, { Component } from 'react';
import marked from 'marked';

import Input from './components/input/input';
import Output from './components/output/output';

class Home extends Component{
    
    state = {
        input: `
        Welcome to my Markdown Previewer
        ==
        I built this with Next.js and marked.js

        *I didn't need React for this simple project*

        **I used it because I wanted to!**
    `
    };

    componentDidMount(){
        const script = document.createElement("script");
        script.src = 'https://cdn.freecodecamp.org/testable-projects-fcc/v1/bundle.js';
        script.async = true;
        document.body.appendChild(script);
    }

    changeListener(event) {
        this.setState({
            input: event.target.value
        })
    }

    getMarkdown = () =>{
        const markdown = marked(this.state.input, {sanitize: true});
        return {__html: markdown};
    }

    render() {

        return (
            <div >
                <h1>Hallo with Next.js and Marked.js</h1>
                    <Input change={(event) => this.changeListener(event) } />
                    <Output setMarkdown={this.getMarkdown()} />
                    <style jsx>{`
                        div {
                            width: 800px;
                            margin: 75px auto;
                            border; 2px solid black;
                            text-align: center;
                        }
                    `}</style>
            </div>
        );
    }
}

export default Home;